export function SpriteFrom(srcable) {
    return function (target, propertyKey) {
        var src = (srcable
            ? typeof srcable === "string"
                ? { src: srcable }
                : srcable
            : { src: undefined }).src;
        if (!target.spritesDecor) {
            target.spritesDecor = [];
        }
        target.spritesDecor.push({
            propertyKey: propertyKey,
            src: src,
        });
    };
}
